* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.4;
  color: #444;
  background: #fff;
  height: 100vh;
}

a {
  display: block;
  padding: 10px 40px;
  text-decoration: none;
  outline: none;
}

.App {
  background: gray;
  height: 10vh;
  display: flex;
  color: white;
  font-weight: 600;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}

.inner {
  width: 100%;
  padding: 7rem 10px 4rem;
}

.form-container {
  display: flex;
  width: 100%;
  max-width: 600px;
  display: flex;
  border-radius: 1px;
  height: 100%;
  justify-content: center;
}

.input-text {
  display: block;
  justify-content: center;
  font-size: 1rem;
  font-weight: 400;
  width: 100%;
  max-width: 320px;
  border-radius: 10px;
  height: 55px;
  outline: none;
  border: none;
}

.input-text::placeholder {
  color: #000;
  text-align: center;
}

.btn {
  position: relative;
  left: 260px;
  border: none;
  padding: 13px 5px;
  top: -49px;
  width: 50px;
  height: 50x;
  cursor: pointer;
  border-radius: 10px;
  background-color: white;
}

.active-link {
  color: white;
  text-decoration: underline;
}

.navBar {
  position: relative;
}

.item-container {
  display: flex;
  justify-content: center;
}

.item-container:first-child {
  margin-top: 50px;
}

.item {
  background: #f4f4f4;
  margin: 5px;
  margin-top: -30px;
  width: 100%;
  max-width: 500px;
  position: relative;
  word-break: break-all;
  padding: 30px 20px;
  cursor: pointer;
  list-style-type: none;
}

.item-button {
  align-items: center;
  font-size: 12px;
  color: gray;
  background: white;
  border: none;
  font-weight: bold;
  cursor: pointer;
  float: right;
  outline: none;
  border-radius: 10px;
  height: 45px;
  width: 45px;
  margin: -10px 0 0 10px;
}

.item-button:hover {
  background: #f1f3f4;
}

.textInput {
  width: 70%;
  padding: 10px;
  border: 1px solid #dfdfdf;
}
